<template>
    <div class="listdemo08">
        <transition appear mode="out-in">
            <div>
                <!---->
                <v-list></v-list>
                <!---->
                <v-child :opts="arr1" class="arr1">
                    <div class="tag" v-html="tagData.tag1"></div>
                    <button @click="changArr1">{{tagData.btn1}}</button>
                </v-child>
                <!---->
                <v-child :opts="arr2" class="arr2">
                    <div class="tag" v-html="tagData.tag2"></div>
                    <button @click="changArr2">{{tagData.btn2}}</button>
                </v-child>
            </div>
        </transition>
    </div>
</template>

<script>
    import VList from '../listDemo08/list'
    import VChild from '../listDemo08/childComponent'
    export default {
        name: '',
        components: {
            VList,
            VChild
        },
        data () {
            return {
                tagData:{
                    tag1:"splice<br/>this.arr1.splice(this.arr1.length-1,1,'()');",
                    btn1:"button1",
                    tag2:"<br/>this.arr2[this.arr2.length-1]='()';",
                    btn2:"button2"
                },
                arr1:[
                    '()',
                    '()',
                    '()',
                    'o(**)',
                    '3'
                ],
                arr2:[
                    '()',
                    '()',
                    '()',
                    'o(**)',
                    '3'
                ]
            }
        },
//              Vue.js 
//              1. vm.items[indexOfItem] = newValue
//              2. vm.items.length = newLength
//              3
//              4splice
        methods:{
            changArr1:function () {
                this.arr1.splice(this.arr1.length-1,1,'()');
            },
            changArr2:function () {
                this.arr2[this.arr2.length-1]='()';
            }
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
    .listdemo08{
        display: flex;
        justify-content: center;
        >div{
            margin-left: -160px;
        }
    }
    button{
        float: right;
        margin: 4px 4px 0 0;
        border: 1px solid rgb(34, 172, 56);
        background: none;
        border-radius: 2px;
        width: 60px;
        height: 22px;
        color: rgb(34, 172, 56);
        transition: all .5s;
        cursor: pointer;
        &:hover{
            background: rgb(34, 172, 56);
            color: #fff;
        }
    }
    .arr1{
        &:after{
            content: '1';
        }
    }
    .arr2{
        &:after{
            content: '2';
        }
    }
</style>



