<template>
    <ul class="listdemo08-list">
        <li v-for="(item,index) in listData" :class="{more:index == listData.length-1}" v-html="item"></li>
    </ul>
</template>

<script>
    export default {
        name: '',
        data () {
            return {
                listData:[
                    '1button11button22',
                    '2button22button112',
                    '3button11button22button1,2',
                    '<span class="exp"></span>button2arr2',
                    '<span class="exp">More</span><a href="https://cn.vuejs.org/v2/guide/list.html#" target="_blank">Vue Official Issue Solve</a>'
                ]
            }
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss">
    .listdemo08-list{
        width: 807px;
        border: 1px solid #cdcdcd;
        border-radius: 5px;
        margin: 0px 0 10px 0;
        li{
            padding: 5px 10px;
            font-size: 14px;
            list-style: none;
            line-height: 28px;
            border-bottom: 1px dashed #ccc;
            &.more{
                border-bottom: none;
            }
            a{
                text-decoration: none;
                color: rgb(34, 172, 56)
            }
            .exp{
                font-weight: bold;
            }
        }
    }
</style>



